$('document').ready(() => {

    //  troca a cor da navbar ao descer
    $(window).scroll(() => {
        // nÃ£o utilizar o this pq buga no parcel build            AQUI --V-- AQUI
        $('nav').toggleClass(' nav-down animated fadeIn', $(window).scrollTop() > $('nav').height())
    })

    // PARALLAX SLIDE 
    $(window).scroll(function () {
        var top_of_element = $(".parallax").offset().top;
        var bottom_of_element = $(".parallax").offset().top + $(".parallax").outerHeight();
        var bottom_of_screen = $(window).scrollTop() + $(window).innerHeight();
        var top_of_screen = $(window).scrollTop();

        if ((bottom_of_screen > top_of_element) && (top_of_screen < bottom_of_element)) {
            $("#parallax-content").addClass('animated bounceInUp')
        } else {
            $("#parallax-content").removeClass('animated bounceInUp')
        }
    });

})
